import React, {useState} from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import Home from './pages/index'
import SignIn from "./pages/Auth/SignIn";
import SignUp from "./pages/Auth/SignUp";
import ExerciseSets from "./pages/Exercise/Sets";
import SetDetail from "./pages/Exercise/SetDetail";
import ExerciseCode from "./pages/Exercise/Code";

import {UserContext} from './contexts'
import Forget from "./pages/Auth/Forget";

function App() {

    const [token, setToken] = useState(localStorage.getItem('token'))

    return (
        <UserContext.Provider value={{
            token,
            setToken
        }}>
            <BrowserRouter className="App">
                <Switch>
                    <Route path={'/forget-password'} component={Forget}/>
                    <Route path={'/signup'} component={SignUp}/>
                    <Route path={'/signin'} component={SignIn}/>
                    <Route path={'/exerciseSets/:sid/:id'} component={ExerciseCode}/>
                    <Route path={'/exerciseSets/:id'} component={SetDetail}/>
                    <Route path={'/exerciseSets'} component={ExerciseSets}/>
                    <Route path="/" component={Home}/>
                </Switch>
            </BrowserRouter>
        </UserContext.Provider>
    );
}

export default App;
