import React, {useEffect, useState} from 'react'
import {withRouter} from 'react-router-dom'
import AceEditor from "react-ace";
import "ace-builds/src-noconflict/mode-html";
import "ace-builds/src-noconflict/theme-tomorrow_night";
import {Button, Modal} from 'antd'

import styles from './Code/index.module.less'
import {restartExerciseApi, startExerciseApi, submitCodeApi} from "../../requests/exercise";
import Browser from "./Code/Browser";

const {confirm} = Modal

function ExerciseCode(props) {

    const [info, setInfo] = useState({
        question:"",
        name:''
    })
    const [code, setCode] = useState("")
    let exerciseId = props.match.params.id

    useEffect(() => {
        startExerciseApi(exerciseId).then(response=>{
            const {question, name, code} = response.data
            setInfo({question, name})
            setCode(code)
        })
    }, [])

    const onSubmit = ()=>{
        submitCodeApi(exerciseId, {
            code,
        }).then(response=>[

        ])
    }

    const onRestart = ()=>{

        confirm({
            title:"是否重置代码",
            cancelText:"取消",
            okText:"确认",
            onOk(){
                restartExerciseApi(exerciseId).then(response=>{
                    setCode(response.data.code)
                })
            }
        })
    }



    return <div className={styles.wrapper}>
        <header className={styles.header}>
            <div></div>
            <div></div>
            <div>
                <a onClick={onRestart} className={styles.textBtn}>重置代码</a>
                <Button
                    type={'primary'}
                    onClick={onSubmit}>提交代码</Button>
            </div>
        </header>
        <main className={styles.main}>
            <div className={styles.question}>
                {info.question}
            </div>
            <div className={styles.editorWrap}>
                <AceEditor
                    editorProps={{
                        fontSize:16
                    }}
                    onChange={value => setCode(value)}
                    theme={"tomorrow_night"}
                    value={code}
                    mode={'html'}
                    style={{height: "100%", width:"100%"}}/>
            </div>
            <div>
                <Browser source={code}/>
            </div>
        </main>
    </div>
}

export default withRouter(ExerciseCode)