import React, {useState} from 'react'
import {Form, Input, Button, Alert, Result, AutoComplete} from 'antd'
import NavBar from "../../components/NavBar";
import {signupApi} from "../../requests/user";


function SignUp() {

    const [form] = Form.useForm()

    const [loading, setLoading] = useState(false)
    const [error, setError] = useState('')
    const [success, setSuccess] = useState(false)
    const [result, setResult] = useState([]);


    const handleSearch = (value: string) => {
        let res = [];
        if (!value || value.indexOf('@') >= 0) {
            res = [];
        } else {
            res = ['gmail.com', '163.com', 'qq.com'].map(domain => `${value}@${domain}`);
        }
        setResult(res);
    };

    const onSubmit = (values) => {
        setLoading(true)
        signupApi(values).then(res => {
            setLoading(false)
            setSuccess(true)
        }).catch(err => {
            setLoading(false)
            setError(err.response.data.msg)
        })
    }

    const children = result.map((email) => (
        <AutoComplete.Option key={email} value={email}>
            {email}
        </AutoComplete.Option>
    ));

    return <div>
        <NavBar/>
        <div className={'main-form'}>
            {!success ? <Form form={form} onFinish={onSubmit}>
                <Form.Item name={'email'} rules={[
                    {required: true, message: "请输入注册邮箱"},
                    {type: "email", message: "请输正确的邮箱"}
                ]}>
                    <AutoComplete
                        onSearch={handleSearch}
                        placeholder={"注册邮箱"}
                        size={'large'}>
                        {children}
                    </AutoComplete>
                </Form.Item>
                <Form.Item name={'password'} rules={[{required: true, message: "请输入密码"}]}>
                    <Input
                        type={'password'}
                        placeholder={"请设置你的密码"}
                        size={'large'}/>
                </Form.Item>
                <Form.Item>
                    <Button
                        loading={loading}
                        htmlType="submit"
                        block={true}
                        size={'large'} type={'primary'}>注册</Button>
                </Form.Item>
                {error ? <Alert message={error}/> : ""}
            </Form> : <Result title="激活邮件已发送至你的邮箱，请前往点击激活链接"
                              extra={
                                  <Button type="primary" key="console">
                                      重新注册
                                  </Button>
                              }/>}

        </div>
    </div>
}

export default SignUp