import React, {useState, useEffect} from 'react'
import {Table} from 'antd'
import {Link} from 'react-router-dom'

import NavBar from "../../components/NavBar";
import {fetchExerciseSetDetail} from "../../requests/exercise";


function ExerciseSetDetail(props) {

    let [exercises, setExercises] = useState([])


    useEffect(()=>{
        fetchExerciseSetDetail(props.match.params.id).then(response => {
            setExercises(response.data.exercises)
        })
    }, [])



    return <div>
        <NavBar/>

        <div className={'main-container'}>
            <Table
                columns={[
                    {
                        title: '姓名',
                        dataIndex: 'name',
                        key: 'name',
                    },
                    {
                        title: '年龄',
                        dataIndex: 'age',
                        key: 'age',
                    },
                    {
                        title: '操作',
                        dataIndex: 'address',
                        key: 'address',
                        render: (text, record) => {
                            return <Link
                                to={'/exerciseSets/' + props.match.params.id + '/' + record.id}>前往练习</Link>
                        }
                    },
                ]}
                dataSource={exercises}
            />
        </div>
    </div>
}

export default ExerciseSetDetail