import React from 'react'
import {PageHeader, Row, Col} from 'antd'
import NavBar from "../components/NavBar";

import './index.less'

function Home() {
    return <div>
        <NavBar/>

        <div className={'home-banner'}>
            <div className={'banner-title-wrapper'}>
                <h1>My Code Game</h1>
                <p>云上练习编程，无需安装任何环境，打开网页即可编程</p>
            </div>
        </div>

    </div>
}


export default Home