import React, {useState} from 'react'
import {Form, Steps, Input, Button} from 'antd'
import DefaultLayout from "../../layouts/DefaultLayout";
import {forgetEmail} from "../../requests/user";

const {Step} = Steps

export default function () {

    let [current] = useState(0)
    let [loading, setLoading] = useState(false)
    let [emailForm] = Form.useForm()


    let onSubmitEmail = (values) => {
        setLoading(true)
        forgetEmail()
    }


    return <DefaultLayout title={"忘记密码"}>
        <div className={'main-form'}>
            <Steps progressDot size="small" current={current}>
                <Step title="输入要重置密码的邮箱"/>
                <Step title="验证信息"/>
                <Step title="完成"/>
            </Steps>

            {current == 0 && <Form className={"mt50"} form={emailForm}>
                <Form.Item name={'请输入要重置密码的账号'} rules={[
                    {required: true, message: "请输入要重置密码的账号"},
                ]}>
                    <Input
                        placeholder={'请输入要重置密码的账号'}
                        size={'large'}/>
                </Form.Item>
                <Form.Item>
                    <Button
                        size={'large'}
                        onClick={onSubmitEmail}
                        block={true}
                        type={"primary"}>
                        进入下一步
                    </Button>
                </Form.Item>
            </Form>}

            {current == 1 && <Form>

            </Form>}

        </div>
    </DefaultLayout>
}