import axios from 'axios';
import Qs from 'qs'


var instance = axios.create({});

const env = window.location.port == 3000 ?  "dev" :"production"
const host =  env == 'production' ? '39.104.184.236' : '127.0.0.1'
const port =  5000
instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
instance.defaults.headers.put['Content-Type'] = 'application/x-www-form-urlencoded';


instance.defaults.baseURL = `http://${host}:${port}`;
instance.interceptors.request.use(function (config) {

    let token = localStorage.getItem('token')
    if (token){
        config.headers.Authorization = `${token}`
    }

    return config
})

instance.interceptors.response.use(function (response) {
    return response;
}, function (error) {



    if(!error.response){
        error.response={
            data:{
                msg:"网络异常"
            }
        }
    }else {

        if(error.response.status == 401){
        }

    }
    return Promise.reject(error);
});


export default {
    get(url, params) {
        return instance.get(url, {params})
    },
    post(url, data) {
        return instance.post(url, Qs.stringify(data))
    },
    put(url, data) {
        return instance.put(url, Qs.stringify(data))
    },
    delete(url) {
        return instance.delete(url)
    },

    baseUrl: instance.defaults.baseURL,
    host,
    port,
    instance

}